import React from "react";
import CountUp from "react-countup";
import schoolicon from "../../images/schoolicon.png";
import axios from "axios";

const proto = "http://";
const endPoint = "counter";
const url = `${proto}34.29.141.94/`;

export default class CounterCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      count: [],
    };
    this.componentDidMount = this.componentDidMount.bind(this);
  }

  componentDidMount() {
    axios
      .get(`${url}${endPoint}`)
      .then((res) => {
        this.setState({ count: res.data.data });
        // console.log(res.data.data)
      })
      .catch((res) => console.log(res));
  }

  render() {
    const { count } = this.state;
    return (
      <>
        <div className="counter-bg">
          <div className="container">
            <div className="row">
              {count.map((item, index) => {
                return (
                  <SchoolCounter
                    key={index}
                    title={item.name}
                    count={item.count}
                  />
                );
              })}
            </div>
          </div>
        </div>
      </>
    );
  }
}

class SchoolCounter extends React.Component {
  render() {
    const { title, count } = this.props;
    return (
      <>
        <div className="col-md-4 text-center mt-3">
          <div className="d-flex">
            <img src={schoolicon} alt="icon" className="school-icon" />
            <div>
              <CountUp
                className="counter-font"
                start={0}
                end={count}
                suffix=" +"
              ></CountUp>
              <h6 className="reg-school ms-3">{title}</h6>
            </div>
          </div>
        </div>
      </>
    );
  }
}
